.App {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  border-radius: 10px;
}

.card-container {
  overflow: hidden;
  background-color: #23252c;
  color: white;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr repeat(2, max-content) min-content;
  grid-template-columns: min-content;
  gap: 3rem;
  border-radius: 10px;
}

